@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);

  &__content{
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
  }

  &__close{
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    @include m.modals(v.$negro);
  }
}
